<template>
	<div class="slide-wrapper d-flex justify-content-center align-items-center">
		<div class="container-fluid box d-flex justify-content-center align-items-center">
			<div class=" text-wrapper w-100 d-flex flex-column align-items-center justify-content-center">
				<p>{{text1}}</p>
				<p>{{text2}}</p>
				<p>{{text3}}</p>
				<div class="form w-25">
					<div class="mb-3">
					<label for="name" class="form-label">Имя</label>
					<input v-model="name" type="text" required class="form-control" id="name" placeholder="vasja">
					</div>
					<div class="mb-3">
					<label for="email" class="form-label">Email</label>
					<input v-model="email" type="email" required class="form-control" id="email" placeholder="vasja@posta.ru">
					</div>
					<div class="mb-3">
						<label for="brif" class="form-label">Расскажи, чем сможешь помочь</label>
						<textarea v-model="msg" required placeholder="Знаю javascript, умею работать в PS и т.д." class="form-control" id="brif" rows="3"></textarea>
					</div>
					<button :disabled="!email||!name||!msg" @click="modal" class="btn btn-primary" type="button">Возьмите меня</button>

				</div>
			</div>
		</div>
		<v-dialog/>

	</div>
</template>

<script>
import anime from 'animejs/lib/anime.es.js';
export default {
	data: () => {
		return {
			name: '',
			email: '',
			msg: '',
			text1: '',
			text2: '',
			text3: '',
			text4: '',
			text5: '',
			text6: '',
			text7: '',
		}
	},
	mounted () {
		this.animation1();
	},

	methods: {
		modal() {
			this.$modal.show('dialog', {
				title: 'Отправлено!',
				text: 'Мы обязательно рассмотрим твое письмо и дадим обратную связь',
				buttons: [
					{
						title: 'Ок',
						handler: () => {
							this.$modal.hide('dialog')
						}
					}
				]
			})
		},
		animation1 () {
			const text1 = 'Наша команда ищет соратников 🙋‍♀️🙋‍♂️...';
			const text2 = 'Если ты умеешь писать код 💻, дизайнить 👨‍🎨, анализировать 🧠, редактировать текст ✏️, то ты нам нужен 👁';
			const text3 = 'Заполни форму ✉️ и мы тебе напишем (правда, правда)';
			const totalLenght = text1.length + text2.length + text3.length;
			let progress = {lenght: 0}

			anime({
				delay: 1000,
				duration:6000,
				targets: progress,
				lenght: totalLenght,
				round: 1,
				easing: 'linear',
				update: () => {
					if (progress.lenght < text1.length) {
						this.text1 = text1.substr(0,progress.lenght)
					} else if (progress.lenght < (text1.length + text2.length)) {
						this.text1 = text1
						this.text2 = text2.substr(0,progress.lenght - text1.length)
					} else if (progress.lenght < (text1.length + text2.length + text3.length)) {
						this.text1 = text1
						this.text2 = text2
						this.text3 = text3.substr(0,progress.lenght - text1.length - text2.length)
					}
				},
				begin: () =>  {
					this.text1 = "";
					this.text2 = "";
					this.text3 = "";
					
				},
				complete: () => {
					this.text1 = text1
					this.text2 = text2
					this.text3 = text3
					//this.animation2()
				}
			});
		},
		animation2 () {
			const text5 = 'Мы решаем проблему взаимодействия между предприятиями и вузом в проектной деятельности 🆘';
			let progress = {length: 0}

			anime({
				delay: 500,
				duration:2500,
				targets: progress,
				length: text5.length,
				round: 1,
				easing: 'linear',
				update: () => {
					if (progress.length < text5.length) {
						this.text5 = text5.substr(0,progress.length)
					}
				},
				begin: () =>  {
					this.text5 = "";
				},
				complete: () => {
					this.text5 = text5
					this.animation3()
				}
			});
		},
		animation3 () {
			const text6 = 'Помогаем найти 🔍 предприятию исполнителя для решения проблемы';
			let progress = {length: 0}

			anime({
				delay: 500,
				duration:2500,
				targets: progress,
				length: text6.length,
				round: 1,
				easing: 'linear',
				update: () => {
					if (progress.length < text6.length) {
						this.text6 = text6.substr(0,progress.length)
					}
				},
				begin: () =>  {
					this.text6 = "";
				},
				complete: () => {
					this.text6 = text6
					this.animation4()
				}
			});
		},
		animation4 () {

			const text7 = 'а студенту 🧑🏻‍🎓 - реальные задачи для практики';
			let progress = {length: 0}

			anime({
				delay: 500,
				duration:2500,
				targets: progress,
				length: text7.length,
				round: 1,
				easing: 'linear',
				update: () => {
					if (progress.length < text7.length) {
						this.text7 = text7.substr(0,progress.length)
					}
				},
				begin: () =>  {
					this.text7 = "";
				},
				complete: () => {
					this.text7 = text7
				}
			});
		}
	}
}
</script>

<style scoped>
	
	.slide-wrapper {
		width: 100vw;
		height: 100vh;
		font-size: 1.5vw;
		color: #3f3f3f;
		font-family: 'Anonymous Pro', monospace !important;
		background-color: #f8f9fa;
	}

	.one {
		border-radius: 5% 0 0 5%;
		background-color: #9cade5;
		
	}
	.two {
		background-color: #f15053;
	}
	.three {
		background-color: #6f6f6f;
		border-radius: 0% 5% 5% 0%;
	}

</style>