<template>
	<div class="slide-wrapper d-flex justify-content-center align-items-center">
		<div class="box d-flex justify-content-center align-items-center">
			<div class="text-wrapper d-flex flex-column justify-content-center">
				<p>
				<span class="one">{{text1}}</span>
				<span class="two">{{text2}}</span>
				<span class="three">{{text3}}</span>
				{{text4}}
				</p>
				<p>{{text5}}</p>
				<p>{{text6}}</p>
				<p>{{text7}}</p>
			</div>
		</div>
	</div>
</template>

<script>
import anime from 'animejs/lib/anime.es.js';
export default {
	data: () => {
		return {
			text1: '',
			text2: '',
			text3: '',
			text4: '',
			text5: '',
			text6: '',
			text7: '',
		}
	},
	mounted () {
		this.animation1();
	},

	methods: {
		animation1 () {
			const text1 = 'project';
			const text2 = 'store';
			const text3 = '📝';
			const text4 = ' - это интеграционный сервис для создания проектов';
			const totalLenght = text1.length + text2.length + text3.length + text4.length;
			let progress = {lenght: 0}

			anime({
				delay: 750,
				duration:2000,
				targets: progress,
				lenght: totalLenght,
				round: 1,
				easing: 'linear',
				update: () => {
					if (progress.lenght < text1.length) {
						this.text1 = text1.substr(0,progress.lenght)
					} else if (progress.lenght < (text1.length+ text2.length)) {
						this.text1 = text1
						this.text2 = text2.substr(0,text1.length+progress.lenght)
					} else if (progress.lenght < (text1.length+ text2.length+text3.length)) {
						this.text1 = text1
						this.text2 = text2
						this.text3 = text3.substr(0,text1.length+ text2.length+progress.lenght)
					} else if (progress.lenght < (text1.length+ text2.length+text3.length+ text4.length)) {
						this.text1 = text1
						this.text2 = text2
						this.text3 = text3
						this.text4 = text4.substr(0,text1.length+ text2.length+text3.length+progress.lenght)
					} else {
						this.text1 = text1
						this.text2 = text2
						this.text3 = text3
						this.text4 = text4
					}
				},
				begin: () =>  {
					this.text1 = "";
					this.text2 = "";
					this.text3 = "";
					this.text4 = "";
				},
				complete: () => {
					this.text1 = text1
					this.text2 = text2
					this.text3 = text3
					this.text4 = text4
					this.animation2()
				}
			});
		},
		animation2 () {
			const text5 = 'Мы решаем проблему взаимодействия между предприятиями и вузом в проектной деятельности 🆘';
			let progress = {length: 0}

			anime({
				delay: 500,
				duration:2500,
				targets: progress,
				length: text5.length,
				round: 1,
				easing: 'linear',
				update: () => {
					if (progress.length < text5.length) {
						this.text5 = text5.substr(0,progress.length)
					}
				},
				begin: () =>  {
					this.text5 = "";
				},
				complete: () => {
					this.text5 = text5
					this.animation3()
				}
			});
		},
		animation3 () {
			const text6 = 'Помогаем найти 🔍 предприятию исполнителя для решения проблемы';
			let progress = {length: 0}

			anime({
				delay: 500,
				duration:2500,
				targets: progress,
				length: text6.length,
				round: 1,
				easing: 'linear',
				update: () => {
					if (progress.length < text6.length) {
						this.text6 = text6.substr(0,progress.length)
					}
				},
				begin: () =>  {
					this.text6 = "";
				},
				complete: () => {
					this.text6 = text6
					this.animation4()
				}
			});
		},
		animation4 () {

			const text7 = 'а студенту 🧑🏻‍🎓 - реальные задачи для практики';
			let progress = {length: 0}

			anime({
				delay: 500,
				duration:2500,
				targets: progress,
				length: text7.length,
				round: 1,
				easing: 'linear',
				update: () => {
					if (progress.length < text7.length) {
						this.text7 = text7.substr(0,progress.length)
					}
				},
				begin: () =>  {
					this.text7 = "";
				},
				complete: () => {
					this.text7 = text7
				}
			});
		}
	}
}
</script>

